<template>
<div>
  <el-table
    :data="tableDate"
    style="width: 95%"
    >
    <el-table-column
      prop="updatedDate"
      sortable
      label=""
      :formatter="formatter"
      width="180">
    </el-table-column>
    <el-table-column
        sortable
      prop="title"
      label=""
      width="180">
    </el-table-column>
     <el-table-column
        label=""
        width="120">
        <!--  -->
        <template slot-scope="showPsScope">
            <el-tooltip class="item" effect="light" :content="showPsScope.row.ps" placement="top-end"  >
                 <el-button type="text" circle size="small" :disabled="!showPsScope.row.ps" ></el-button>
            </el-tooltip>
        </template>
    </el-table-column>
    <el-table-column
      prop="value"
      label="">
    </el-table-column>
    <el-table-column
        fixed="right"
        label=""
        width="180">
        <template slot-scope="scope">
            <!-- || -->
             <el-button
                v-if="canTranfer"
                @click.native.prevent="showTransferDialog(scope.row)"
                type="text"
                size="small">
                
            </el-button>
             <el-button
                @click.native.prevent="showUpdateDialog(scope.row)"
                type="text"
                size="small">
                
            </el-button>
            <el-button
                @click.native.prevent="deleteRow(scope.row)"
                type="text"
                size="small">
                
            </el-button>
        </template>
    </el-table-column>
  </el-table>

    <!--  -->
        <el-dialog  :visible.sync="dialogUpdateLog">
            <h2 style="margin-bottom:40px"></h2>
            <el-form :rules="rules" ref="oneUpdateForm" :model="oneUpdateForm" label-width="120px">
                <el-form-item label=":" prop="loginName">
                    <el-input v-model="oneUpdateForm.loginName" style="width:200px;float:left"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="verifyCode">
                    <el-input type="password" v-model="oneUpdateForm.verifyCode" style="width:200px;float:left"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="title">
                    <el-input v-model="oneUpdateForm.title" style="width:200px;float:left"></el-input>
                </el-form-item>
                <el-form-item label="" prop="ps">
                    <el-input  v-model="oneUpdateForm.ps"></el-input>
                </el-form-item>
                <el-form-item label="" prop="logValue">
                    <el-input type="textarea" v-model="oneUpdateForm.logValue"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="updateMessage(oneUpdateForm)"></el-button>
                    <el-button @click="dialogUpdateLog=false"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
             <!--  -->
        <el-dialog  :visible.sync="transferVisible">
            <h2 style="margin-bottom:40px"></h2>
            <el-form :rules="rules" ref="oneTranserForm" :model="oneTranserForm" label-width="120px">
                <el-form-item label=":" prop="loginName">
                    <el-input v-model="oneTranserForm.loginName" style="width:200px;float:left"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="verifyCode">
                    <el-input type="password" v-model="oneTranserForm.verifyCode" style="width:200px;float:left"></el-input>
                </el-form-item>
                <el-form-item label=":" style="width:100px;" prop="selectType">
                    <el-select v-model="oneTranserForm.selectType" style="width:150px" placeholder="">
                        <el-option label="" value="DONE"></el-option>
                        <el-option label="" value="BUG"></el-option>
                        <el-option label="" value="TODO"></el-option>
                        <el-option label="" value="MEMBER_SAY"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="ps">
                    <el-input  v-model="oneTranserForm.ps"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="tranferMessage(oneTranserForm)"></el-button>
                    <el-button @click="transferVisible=false"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
</div>
</template>

<script>
import service from '@/api/file.services'
export default {
    name: 'show-log',
    props:["projectType", "type", "tableDate","canTranfer"],
    data() {
        return {
            dialogUpdateLog: false,
            transferVisible: false,
            oneTranserForm: {
                id: '',
                verifyCode: '',
                loginName: '',
                ps: '',
                // selectType:''
            },
            oneUpdateForm:{
                id: '',
                verifyCode: '',
                title: '',
                logValue: '',
                loginName: '',
                ps: ''
            },
            rules: {
                loginName: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                verifyCode: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                // selectType: [
                //     { required: true, message: '', trigger: 'change' }
                // ],
                title: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                logValue: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            }
        }
    },
    mounted(){
        this.loadDate()
    },
    methods:{
        showTransferDialog(row) {
            //
            // console.log(row)
            this.transferVisible = true
            this.oneTranserForm.logValue = row.value
            this.oneTranserForm.loginName = row.loginName
            this.oneTranserForm.ps = row.ps
            this.oneTranserForm.projectType = row.projectType
            this.oneTranserForm.id = row.id
            this.oneTranserForm.selectType = row.type
        },
        showUpdateDialog(row) {
            // row
            // console.log(row)
            this.dialogUpdateLog  = true
            //
            // oneUpdateForm.verifyCode = row.verifyCode
            this.oneUpdateForm.title = row.title
            this.oneUpdateForm.id = row.id
            this.oneUpdateForm.logValue = row.value
            this.oneUpdateForm.loginName = row.loginName
            this.oneUpdateForm.ps = row.ps
            this.oneUpdateForm.projectType = row.projectType
            // console.log(this.oneUpdateForm)

        },
        loadDate() {
            // 
        },
        formatter(row, column) {
            // new Date('' + row.updatedDate)
            return (row.updatedDate + '').substring(0,10)
            // return service.changeToBJTime(row.updatedDate);//.substring(0,10);
        },
        tranferMessage(row) {
            //
            // console.log(row)
            if(row.id){
                const url = '' + service.getServiceIp() + "/api/message/tranfer"
                this.$http.put(url,{
                        loginName: row.loginName,
                        verifyCode: row.verifyCode,
                        token: '',
                        id:row.id,
                        ps:row.ps,
                        type: row.selectType,
                        projectType:row.projectType
                    }).then(function(res){
                        //
                        if(res.data.returnCode.startsWith("200")){
                            //
                            this.$message({
                                type:'success',
                                showClose:true,
                                message:''
                            })
                            this.$emit('refreshbizlines',false);
                        }else{
                            this.$message({
                                type:"error",
                                showClose:true,
                                message:""
                            })
                        }
                })
            }else{
                this.$message({
                    type:"error",
                    showClose:true,
                    message:""
                    })
                this.$emit('refreshbizlines',false);

            }
            this.transferVisible=false
        },

        updateMessage(row) {
            //
            if(row.id) {
                 const url = '' + service.getServiceIp() + "/api/message/update"
                this.$http.put(url,{
                        projectType: row.projectType,
                        loginName: row.loginName,
                        verifyCode: row.verifyCode,
                        token: '',
                        id:row.id,
                        ps:row.ps,
                        value:row.logValue,
                        title:row.title
                    }).then(function(res){
                        //
                        if(res.data.returnCode.startsWith("200")){
                            //
                            this.$message({
                                type:'success',
                                showClose:true,
                                message:''
                            })
                            this.$emit('refreshbizlines',false);
                        }else{
                            this.$message({
                                type:"error",
                                showClose:true,
                                message:""
                            })
                        }
                })
            }else {
                this.$message({
                    type:"error",
                    showClose:true,
                    message:""
                    })

                this.$emit('refreshbizlines',false);
            }
            this.transferVisible=false
        },
        deleteRow(row) {
            // rows.splice(index, 1);
            //loginName
            //
            let code = '';
            this.$prompt('(:'+row.loginName+')', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                inputType: 'password',
                inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+?/,
                // inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
                inputErrorMessage: ''
                }).then(({ value }) => {
                    // this.$message({
                    //     type: 'success',
                    //     message: ': ' + value
                    // });
                    if(row.id){

                        const url = '' + service.getServiceIp() + "/api/message/" + row.id
                        this.$http.delete(url,{
                            body:{
                                loginName: row.loginName,
                                verifyCode: value,
                                token: '',
                                projectType: row.projectType
                            }
                        }).then(function(res){
                            //
                            if(res.data.returnCode.startsWith("200")){
                                //
                                this.$message({
                                    type:'success',
                                    showClose:true,
                                    message:''
                                })
                                this.$emit('refreshbizlines',false);
                            }else{
                                this.$message({
                                    type:"error",
                                    showClose:true,
                                    message:""
                                })
                            }
                        })
                    }
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                    this.verifyCode = ''
            });
        },
        showInputPs() {
            //
        },
    }
}
</script>
<style>

</style>




