<template>
<div>
    <el-button type="primary " icon="el-icon-edit" @click="showAddMessageDialog" style="float:right"></el-button>
    <el-tabs v-model="type" @tab-click="handleClick" style="width:95%" >
        <el-tab-pane label="" name="DONE"><h3 style="color:#33A1C9"></h3>
            <show-log
                v-bind:canTranfer="true"
                v-bind:projectType="projectType"
                v-on:refreshbizlines="getTableDate" keep-alive
                v-bind:type="type"
                v-bind:tableDate="tableDate">
            </show-log>
        </el-tab-pane>
        <el-tab-pane label="" name="BUG"><h3 style="color:#C80000"></h3>
            <show-log
                v-bind:canTranfer="true"
                v-bind:projectType="projectType"
                v-on:refreshbizlines="getTableDate" keep-alive
                v-bind:type="type"
                v-bind:tableDate="tableDate">
            </show-log>
        </el-tab-pane>
        <el-tab-pane label="" name="TODO"><h3 style="color:#FF8000 "></h3>
            <show-log
                v-bind:canTranfer="true"
                v-bind:projectType="projectType"
                v-on:refreshbizlines="getTableDate" keep-alive
                v-bind:type="type"
                v-bind:tableDate="tableDate">
            </show-log>
        </el-tab-pane>
        <el-tab-pane label="" name="MEMBER_SAY"><h3 style="color:#215E21 "></h3>
            <show-log
                v-bind:canTranfer="false"
                v-bind:projectType="projectType"
                v-on:refreshbizlines="getTableDate" keep-alive
                v-bind:type="type"
                v-bind:tableDate="tableDate">
            </show-log>
        </el-tab-pane>
    </el-tabs>
            <!--  -->
        <el-dialog  :visible.sync="dialogAddLog">
            <h2 style="margin-bottom:40px"></h2>
            <el-form :rules="rules" ref="form" :model="form" label-width="120px">
                <el-form-item label=":" prop="loginName">
                    <el-input v-model="form.loginName" style="width:200px;float:left"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="verifyCode">
                    <!-- <div style="width:200px;float:left">23333</div>
                    <div >
                        <el-input v-model="form.verifyCode" ></el-input>
                    </div> -->
                    <el-input type="password" v-model="form.verifyCode" style="width:200px;float:left"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="title">
                    <el-input v-model="form.title" style="width:200px;float:left"></el-input>
                </el-form-item>
                <el-form-item label=":" style="width:100px;" prop="selectType">
                    <el-select v-model="form.selectType" style="width:150px" placeholder="">
                        <el-option label="" value="DONE"></el-option>
                        <el-option label="" value="BUG"></el-option>
                        <el-option label="" value="TODO"></el-option>
                        <el-option label="" value="MEMBER_SAY"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="ps">
                    <el-input  v-model="form.ps"></el-input>
                </el-form-item>
                <el-form-item label="" prop="logValue">
                    <el-input type="textarea" v-model="form.logValue"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit('form')"></el-button>
                    <el-button @click="cancelAdd"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
</div>
</template>
<script>
import showLog from './show-log.vue'
import services from '@/api/file.services'
export default {
    name: 'qlh-develop-log',
    props:["projectType"],
    data() {
        return {
            dialogAddLog: false,
            // projectType: '',
            type: 'DONE',
            _tableDate: [],
            tableDate: [],
            // 
            // codeprojectType
            form: {
                verifyCode: '',
                title: '',
                selectType: '',
                logValue: '',
                loginName: '',
                ps:''
            },
            rules: {
                loginName: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                verifyCode: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                title: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                selectType: [
                    { required: true, message: '', trigger: 'change' }
                ],
                logValue: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            }
        }
    },
    mounted(){
        this.getTableDate()
    },
    methods: {
      getTableDate(needShow) {
          console.log(this.projectType)
          const url = '' + services.getServiceIp()+"/api/message"+"?projectType="+this.projectType
          this.$http.get(url,{}).then(function(res){
              if(res.data.returnCode.startsWith("200")){
                    // while(this._tableDate.>0){
                    //     this._tableDate.pop()
                    // }                   
                  this._tableDate = res.data.data;
                  //activeName_tableDate
                  this.tableDate = []
                    // while(this.tableData.length>0){
                    //     this.tableData.pop()
                    // }                
                  if(this._tableDate){
                    for(let i=0;i<this._tableDate.length;i++){
                        if(this._tableDate[i].type==this.type){
                            this.tableDate.push(this._tableDate[i])
                        }
                    }
                  }
                //   console.log(this._tableDate)
                  if(needShow==false){

                  }else{
                      this.$message({
                          type:"success",
                          message:""
                      })
                  }
              }else{
                  if(needShow==false){

                  }else{
                      this.$message({
                          type:"error",
                          message:""
                      })
                  }
              }
          })
      },
      handleClick(tab, event) {
        // console.log(tab);
        //
        // this.getTableDate()
        // 
        this.tableDate = []
        if(this._tableDate){
            for(let i=0;i<this._tableDate.length;i++){
                if(this._tableDate[i].type==this.type){
                    this.tableDate.push(this._tableDate[i])
                }
            }
        }
      },
      showAddMessageDialog() {
          //API
          //
          this.dialogAddLog = true
          //
      },
      cancelAdd() {
          this.dialogAddLog = false
      },
      onSubmit(form) {
          //
        this.$refs[form].validate((valid) => {
          if (valid) {
            //
            // this.verifyCode =  '',
            // this.loginName = '',
            //
            const url = '' + services.getServiceIp()+"/api/message"
            this.$http.post(url,{
                projectType:this.projectType,
                type:this.form.selectType,
                value: this.form.logValue,
                verifyCode:this.form.verifyCode,
                loginName:this.form.loginName,
                title: this.form.title,
                ps: this.ps,
                token:''
            }).then(function(res) {
                if(res.data.returnCode.startsWith('200')) {
                    //
                    this.$message({
                        showClose: true,
                        type:"success",
                        message:""
                    })
                    this.getTableDate(false);
                }else{
                  this.$message({
                      type:"error",
                      showClose: true,
                      message:""
                  })
                }
            })

            this.title = '',
            this.selectType = '',
            this.logValue =  ''
            this.dialogAddLog = false
            return true;
          } else {
            return false;
          }
        });

      },
    },
    components: {
        showLog
    }
}
</script>



