<template>
    <div style="width:800px; margin-left:auto; margin-right:auto;">
        
        <!-- list:QUEIDList -->
        <!--  -->
        <el-steps :active="active" align-center style="margin-top:20px">
            <el-step title="" description="1"></el-step>
            <el-step title="" description="2"></el-step>
            <el-step title="" description="3"></el-step>
        </el-steps>
        <div style="margin-top:80px">
            <div v-if="active==0">
                <!--  -->
                <el-select v-model="type" placeholder="" style="width:400px">
                    <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
                            </div>
            <div v-if="active==1">
                <!-- : -->
                <!--  -->
                <qlh-apply-105-ask 
                    v-on:Next="Next"
                    v-if="type=='APPLY_105_ASK'" 
                    ref="childForm">
                </qlh-apply-105-ask>
                <qlh-apply-week-battle-gu 
                    v-on:Next="Next"
                    v-if="type=='APPLY_WEEK_BATTLE_GU'" 
                    ref="childForm">
                </qlh-apply-week-battle-gu>
                <qlh-apply-105-gu 
                    v-on:Next="Next"
                    v-if="type=='APPLY_105_GU'" 
                    ref="childForm">
                </qlh-apply-105-gu>
            </div>
            <div v-if="active==2">
                <!--  -->
                <div style="margin-top:80px;">
                    gaygaypy~~~
                </div>
            </div>
        </div>
        <div style="margin-top:50px;">
            <!--  -->
            <el-button style="margin-left:200px;float:left" v-if="active>0" @click="Pre" ></el-button>
            <el-button style="margin-right:200px;float:right" v-if="active<1" @click="Next"></el-button>
            <el-button style="margin-right:200px;float:right" v-if="active==1" @click="postAsk"></el-button>
            <!--  -->
            <el-button style="margin-right:200px;float:right" v-if="active==2" @click="First" ></el-button>

        </div>
    </div>
</template>

<script>
import  qlhApply105Ask from './qlh-apply/qlh-apply-105-ask.vue'
import  qlhApplyWeekBattleGu from './qlh-apply/qlh-apply-week-battle-gu.vue'
import  qlhApply105Gu from './qlh-apply/qlh-apply-105-gu.vue'

export default {
    name:'qlh-resource-apply',
    // props:["active"],
    data() {
        return {
            active: 0,
            projectType: 'QLH',
            type: 'APPLY_105_ASK',
            options: [
                {
                    value: 'APPLY_105_ASK',
                    label: '105'
                }, 
                {
                    value: 'APPLY_WEEK_BATTLE_GU',
                    label: ''
                },
                {
                    value: 'APPLY_105_GU',
                    label: '105'
                }
                ],
            value: ''
        }
    },
    mounted() {
    },
    methods: {
        Pre() {
            if (this.active > 0 ) this.active--;
        },
        Next() {
            if (this.active < 2) this.active++;
        },
        First() {
            this.active = 0;
        },
        postAsk() {
            this.$refs.childForm.postAsk()//$refs
        }
    },
    components:{
        qlhApply105Ask,
        qlhApplyWeekBattleGu,
        qlhApply105Gu,
    }
}
</script>




